/* Styles généraux */
body {
  background-color: #f5f7fa;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Conteneur principal */
.app-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px 30px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  border-radius: 8px;
}

/* En-tête */
header h1 {
  font-size: 2.5rem;
  color: #007ACC;
  margin-bottom: 5px;
}

header p {
  font-size: 1.2rem;
  margin: 5px 0;
}

/* Navigation */
nav {
  margin-bottom: 30px;
  border-bottom: 2px solid #007ACC;
  padding-bottom: 10px;
}

nav a {
  margin-right: 20px;
  text-decoration: none;
  font-weight: 600;
  color: #007ACC;
  transition: color 0.3s;
}

nav a:hover {
  color: #005b99;
}

/* Sections */
section {
  margin-bottom: 40px;
}

section h2 {
  color: #007ACC;
  border-left: 5px solid #007ACC;
  padding-left: 10px;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

/* Listes */
ul {
  list-style-type: disc;
  padding-left: 20px;
}

ul li {
  margin-bottom: 10px;
  line-height: 1.5;
}

/* Liens dans documents */
section#documents a {
  color: #007ACC;
  font-weight: 600;
  text-decoration: underline;
}

section#documents a:hover {
  color: #005b99;
}
